.wtx-radio-button {
  background-color: #040f1a;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  /* border-right  : solid 1px #2480db; */
  font-size: 14px;
  line-height: 1;
  min-width: 90px;
  padding: 6px 20px;
  position: relative;
  text-align: center;
}

.wtx-radio-button:first-child {
  border-bottom-left-radius: 16px;
  border-top-left-radius: 16px;
}

.wtx-radio-button:last-child {
  border-bottom-right-radius: 16px;
  border-top-right-radius: 16px;
}

.wtx-radio-button .wtx-radio-button__input {
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.wtx-radio-button__checked {
  background-color: #3e81f5;
}
