.wtx-button {
  background-color: #3e81f5;
  border-radius: 24px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  line-height: 1;
  padding: 4px 12px;
  white-space: nowrap;
}

.wtx-button:active {
  background-color: #0e61f2;
}

.wtx-button__actived {
  background-color: #28a80a;
}

.wtx-button__actived:active {
  background-color: #1d7807;
}

.wtx-button:disabled {
  background: #999;
  cursor: not-allowed;
}

.wtx-button__up {
  background-image: url("../assets/up_arrow.png");
  background-position: left 8px center;
  background-repeat: no-repeat;
  background-size: 8px 14px;
  padding-left: 24px;
}

.wtx-button__down {
  background-image: url("../assets/down_arrow.png");
  background-position: right 8px center;
  background-repeat: no-repeat;
  background-size: 8px 14px;
  padding-right: 24px;
}
