.wtx-row {
  display: flex;
}

.wtx-row-flex-top {
  align-items: flex-start;
}

.wtx-row-flex-middle {
  align-items: center;
}

.wtx-row-flex-bottom {
  align-items: flex-end;
}

.wtx-row-flex-start {
  justify-content: flex-start;
}

.wtx-row-flex-center {
  justify-content: center;
}

.wtx-row-flex-end {
  justify-content: flex-end;
}

.wtx-row-flex-space-around {
  justify-content: space-around;
}

.wtx-row-flex-space-between {
  justify-content: space-between;
}
