.wtx-radio {
  background-color: transparent;
  border: solid 1px #2480db;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  line-height: 1;
  margin-right: 10px;
  min-width: 60px;
  padding: 6px;
  position: relative;
  text-align: center;
}

.wtx-radio:last-child {
  margin-right: 0;
}

.wtx-radio .wtx-radio__input {
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.wtx-radio__checked {
  background-color: #3e81f5;
}
