.wtx-card {
  align-items: center;
  background-color: #142134;
  border-bottom: 1px solid #0eff24;
  color: #d4f9ff;
  display: inline-flex;
  min-width: 234px;
  padding: 16px 24px 12px;
  width: auto;
}

.wtx-card:last-child {
  margin-right: 0;
}

.wtx-card .wtx-card__body {
  margin-left: 20px;
}

.wtx-card .wtx-card__title {
  font-size: 14px;
}

.wtx-card .wtx-card__content {
  font-size: 48px;
  line-height: 1;
}
