.wtx-tag {
  background-color: #3e81f5;
  color: #fff;
  display: inline-block;
  font-size: 14px;
  padding: 4px 12px;
  vertical-align: middle;
  white-space: nowrap;
}

.wtx-tag__radius {
  border-radius: 24px;
}

.wtx-tag__ok {
  background: #28a80a url("../assets/tick.png") no-repeat left 8px center/16px 12px;
  padding-left: 30px;
}

.wtx-tag__error {
  background: #fe07ae url("../assets/fork.png") no-repeat left 8px center/12px 12px;
  padding-left: 26px;
}

.wtx-tag__danger {
  background: #fe07ae;
}

.wtx-tag__warning {
  background: #b46a2a;
}

.wtx-tag__disabled {
  background: #999;
}

.wtx-tag__online {
  background: #28a80a url("../assets/wifi_online.jpg") no-repeat left 8px center/20px 14px;
  padding-left: 36px;
}

.wtx-tag__outline {
  background: #999 url("../assets/wifi_outline.jpg") no-repeat left 8px center/20px 14px;
  padding-left: 36px;
}
