.wtx-video-player__wrapper {
  border: solid 1px #87a8ff;
  position: relative;
}

.wtx-video-player__wrapper .wtx-video-player {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.wtx-video-player__wrapper .wtx-video-player__error {
  align-items: center;
  background-color: #000;
  color: #fff;
  display: flex;
  font-size: 24px;
  height: 100%;
  justify-content: center;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
