.rc-select {
  display: inline-block;
  font-size: 12px;
  position: relative;
  width: 100px;
}

.rc-select-disabled,
.rc-select-disabled input {
  cursor: not-allowed;
}

.rc-select-disabled .rc-select-selector {
  opacity: 0.3;
}

.rc-select-show-arrow .rc-select-loading .rc-select-arrow-icon::after {
  animation: rcSelectLoadingIcon 0.5s infinite;
  border: 2px solid #999;
  border-bottom-color: transparent;
  border-radius: 100%;
  border-top-color: transparent;
  box-sizing: border-box;
  height: 12px;
  margin-top: 4px;
  transform: none;
  width: 12px;
}

.rc-select .rc-select-selection-placeholder {
  opacity: 0.4;
}

.rc-select-single .rc-select-selector {
  display: flex;
  position: relative;
}

.rc-select-single .rc-select-selector .rc-select-selection-search {
  width: 100%;
}

.rc-select-single .rc-select-selector .rc-select-selection-search-input {
  width: 100%;
}

.rc-select-single .rc-select-selector .rc-select-selection-item,
.rc-select-single .rc-select-selector .rc-select-selection-placeholder {
  left: 17px;
  pointer-events: none;
  position: absolute;
  right: 34px;
  top: 1px;
}

.rc-select-single:not(.rc-select-customize-input) .rc-select-selector {
  background-color: #fff;
  border: 1px solid #0059ff;
  border-radius: 16px;
  padding: 0 32px 0 17px;
}

.rc-select-single:not(.rc-select-customize-input) .rc-select-selector .rc-select-selection-search-input {
  background: rgba(255, 0, 0, 0.2);
  border: none;
  outline: none;
  width: 100%;
}

.rc-select-multiple .rc-select-selector {
  border: 1px solid #000;
  display: flex;
  flex-wrap: wrap;
  padding: 1px;
}

.rc-select-multiple .rc-select-selector .rc-select-selection-item {
  background: #bbb;
  border-radius: 4px;
  flex: none;
  margin-right: 2px;
  padding: 0 8px;
}

.rc-select-multiple .rc-select-selector .rc-select-selection-item-disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.rc-select-multiple .rc-select-selector .rc-select-selection-search {
  position: relative;
}

.rc-select-multiple .rc-select-selector .rc-select-selection-search-input, .rc-select-multiple .rc-select-selector .rc-select-selection-search-mirror {
  font-family: system-ui;
  padding: 1px;
}

.rc-select-multiple .rc-select-selector .rc-select-selection-search-mirror {
  left: 0;
  position: absolute;
  position: none;
  top: 0;
  visibility: hidden;
  white-space: nowrap;
  z-index: 999;
}

.rc-select-multiple .rc-select-selector .rc-select-selection-search-input {
  background: rgba(255, 0, 0, 0.2);
  border: none;
  outline: none;
  width: 100%;
}

.rc-select-allow-clear.rc-select-multiple .rc-select-selector {
  padding-right: 20px;
}

.rc-select-allow-clear .rc-select-clear {
  position: absolute;
  right: 20px;
  top: 0;
}

.rc-select-show-arrow.rc-select-multiple rc-select-selector {
  padding-right: 20px;
}

.rc-select-show-arrow .rc-select-arrow {
  pointer-events: none;
  position: absolute;
  right: 5px;
  top: 0;
}

.rc-select-show-arrow .rc-select-arrow-icon::after {
  border: 4px solid transparent;
  border-top-color: #1f4cc4;
  content: '';
  display: inline-block;
  height: 0;
  transform: translateY(4px);
  width: 0;
}

.rc-select-focused .rc-select-selector {
  border-color: initial;
}

.rc-select-dropdown {
  background: #fff;
  border: 1px solid #777;
  border-radius: 10px;
  position: absolute;
}

.rc-select-dropdown-hidden {
  display: none;
}

.rc-select-item {
  font-size: 16px;
  line-height: 1.5;
  padding: 4px 16px;
}

.rc-select-item-group {
  color: #999;
  font-size: 80%;
  font-weight: bold;
}

.rc-select-item-option {
  font-size: 14px;
  position: relative;
}

.rc-select-item-option-grouped {
  padding-left: 24px;
}

.rc-select-item-option .rc-select-item-option-state {
  display: none;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 4px;
}

.rc-select-item-option-active {
  background: rgba(0, 0, 0, 0.1);
}

.rc-select-item-option-disabled {
  color: #999;
}

.rc-select-item-empty {
  color: #999;
  text-align: center;
}

.rc-select-selection__choice-zoom {
  transition: all 0.3s;
}

.rc-select-selection__choice-zoom-appear {
  opacity: 0;
  transform: scale(0.5);
}

.rc-select-selection__choice-zoom-appear .rc-select-selection__choice-zoom-appear-active {
  opacity: 1;
  transform: scale(1);
}

.rc-select-selection__choice-zoom-leave {
  opacity: 1;
  transform: scale(1);
}

.rc-select-selection__choice-zoom-leave .rc-select-selection__choice-zoom-leave-active {
  opacity: 0;
  transform: scale(0.5);
}

.rc-select-dropdown-slide-up-enter, .rc-select-dropdown-slide-up-appear {
  animation-duration: 0.3s;
  animation-fill-mode: both;
  transform-origin: 0 0;
  animation-play-state: paused;
  animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
  opacity: 0;
}

.rc-select-dropdown-slide-up-leave {
  animation-duration: 0.3s;
  animation-fill-mode: both;
  transform-origin: 0 0;
  animation-play-state: paused;
  animation-timing-function: cubic-bezier(0.6, 0.04, 0.98, 0.34);
  opacity: 1;
}

.rc-select-dropdown-slide-up-enter.rc-select-dropdown-slide-up-enter-active.rc-select-dropdown-placement-bottomLeft, .rc-select-dropdown-slide-up-appear.rc-select-dropdown-slide-up-appear-active.rc-select-dropdown-placement-bottomLeft {
  animation-name: rcSelectDropdownSlideUpIn;
  animation-play-state: running;
}

.rc-select-dropdown-slide-up-leave.rc-select-dropdown-slide-up-leave-active.rc-select-dropdown-placement-bottomLeft {
  animation-name: rcSelectDropdownSlideUpOut;
  animation-play-state: running;
}

.rc-select-dropdown-slide-up-enter.rc-select-dropdown-slide-up-enter-active.rc-select-dropdown-placement-topLeft, .rc-select-dropdown-slide-up-appear.rc-select-dropdown-slide-up-appear-active.rc-select-dropdown-placement-topLeft {
  animation-name: rcSelectDropdownSlideDownIn;
  animation-play-state: running;
}

.rc-select-dropdown-slide-up-leave.rc-select-dropdown-slide-up-leave-active.rc-select-dropdown-placement-topLeft {
  animation-name: rcSelectDropdownSlideDownOut;
  animation-play-state: running;
}

@keyframes rcSelectDropdownSlideUpIn {
  0% {
    opacity: 0;
    transform: scaleY(0);
    transform-origin: 0% 0%;
  }
  100% {
    opacity: 1;
    transform: scaleY(1);
    transform-origin: 0% 0%;
  }
}

@keyframes rcSelectDropdownSlideUpOut {
  0% {
    opacity: 1;
    transform: scaleY(1);
    transform-origin: 0% 0%;
  }
  100% {
    opacity: 0;
    transform: scaleY(0);
    transform-origin: 0% 0%;
  }
}

@keyframes rcSelectLoadingIcon {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
