.wtx-dialog__theme-dark {
  background: #142134;
  border: 1px solid #207d86;
}

.wtx-dialog__theme-light {
  background: #132f61;
  border: 1px solid #1f4cc4;
}

.wtx-dialog__header {
  align-items: center;
  display: flex;
  padding: 8px;
}

.wtx-dialog__header .wtx-dialog__title {
  color: #0eff24;
  font-size: 16px;
  line-height: 1;
}

.wtx-dialog__header .wtx-dialog__expand {
  flex: auto;
  line-height: 0;
  text-align: right;
}

.wtx-dialog__header .wtx-dialog__expand > * {
  margin-right: 8px;
}

.wtx-dialog__content {
  padding: 0 8px 8px 8px;
}
