@charset "UTF-8";
@font-face {
  font-family: 'DS-Digital-Italic';
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/lcd/DS-DIGII.eot");
  src: url("../fonts/lcd/DS-DIGII.eot?#iefix") format("embedded-opentype"), url("	../fonts/lcd/DS-DIGII.otf") format("opentype"), url("../fonts/lcd/DS-DIGII.svg") format("svg"), url("../fonts/lcds/DS-DIGII.ttf") format("truetype"), url("../fonts/lcd/DS-DIGII.woff") format("woff"), url("../fonts/lcd/DS-DIGII.woff2") format("woff2");
}

.led-num {
  font-family: DS-Digital-Italic, Sans-Serif;
}

body {
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', '微软雅黑', Arial, sans-serif;
  font-size: 14px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

html,
body {
  height: 100%;
  width: 100%;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
section,
dl,
dt,
dd,
ul,
li,
button,
div,
select,
input,
label,
header,
main,
footer,
aside,
table,
td,
tr,
section,
p {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

button,
select,
input {
  border: 0;
  cursor: pointer;
  outline: none;
}

a,
a:focus,
a:hover {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

a:focus,
a:active,
video {
  outline: none;
}

div:focus {
  outline: none;
}

select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background: none;
  border: none;
  text-align: right;
  text-align-last: right;
}

.m-l-8 {
  margin-left: 8px;
}

.m-l-10 {
  margin-left: 10px;
}

.m-t-8 {
  margin-top: 8px;
}

.m-t-10 {
  margin-top: 10px;
}

.m-r-8 {
  margin-right: 8px;
}

.m-r-10 {
  margin-right: 10px;
}

.m-b-8 {
  margin-bottom: 8px;
}

.m-b-10 {
  margin-bottom: 10px;
}

/*****************  float 工具  ******************/
.f-r {
  float: right;
}

.f-l {
  float: left;
}

.clear-fix ::after {
  clear: both;
  content: '';
  display: table;
}

.fade-left-enter {
  opacity: 0;
  transform: translate3d(-30px, 0, 0);
}

.fade-left-enter-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: transform 0.46s, opacity 0.46s;
}

.fade-left-exit {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.fade-left-exit-active {
  opacity: 0;
  transition: transform 0.46s, opacity 0.46s;
  transform: translate3d(-30px, 0, 0);
}

.fade-right-enter {
  opacity: 0;
  transform: translate3d(30px, 0, 0);
}

.fade-right-enter-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: transform 0.46s, opacity 0.46s;
}

.fade-right-exit {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.fade-right-exit-active {
  opacity: 0;
  transition: transform 0.46s, opacity 0.46s;
  transform: translate3d(30px, 0, 0);
}

.fade-top-enter {
  opacity: 0;
  transform: translate3d(0, -30px, 0);
}

.fade-top-enter-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: transform 0.46s, opacity 0.46s;
}

.fade-top-exit {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.fade-top-exit-active {
  opacity: 0;
  transition: transform 0.46s, opacity 0.46s;
  transform: translate3d(0, -30px, 0);
}

.fade-bottom-enter {
  opacity: 0;
  transform: translate3d(0, 30px, 0);
}

.fade-bottom-enter-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: transform 0.46s, opacity 0.46s;
}

.fade-bottom-exit {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.fade-bottom-exit-active {
  opacity: 0;
  transition: transform 0.46s, opacity 0.46s;
  transform: translate3d(0, 30px, 0);
}
